import React from "react"
import Card3d from "../../Card3d/Card3d";
import adidas from '../../../images/adidas.png'
import './SneakerCard.css'

function SneakerCard(props) {
	return (
		<Card3d className="sneaker-card" config={{ mode: 'bottomToTop', offsetStep: '7' }}>
			<div className="sneaker-card__circle" z='0'></div>
			<div className="sneaker-card__sneaker">
				<img src={adidas} alt="" className="sneaker-card__img" style={{ width: '100px', height: '52px' }} />
			</div>
			<h1 className="sneaker-card__title">Adidas ZX</h1>
			<h3 className="sneaker-card__description">FUTURE-READY TRAINERS WITH WRAPPED BOOST FOR EXCEPTION COMFORT.</h3>
			<div className="sneaker-card__sizes">
				<button>39</button>
				<button>40</button>
				<button className="active">42</button>
				<button>44</button>
			</div>
			<button className="sneaker-card__purchase">purchase</button>
		</Card3d>
	)
};

export default SneakerCard;
