import React, { useRef, useState } from "react"

const defaultConfig = {
	maxAngleX: 30,				// Максимальный угол отклонения по X
	maxAngleY: 30,				// Максимальный угол отклонения по Y
	inverseX: false,			// Инвертировать X
	inverseY: false,			// Инвертировать Y
	animationStartDelay: 200,   // Задержка начала анимации (навели мышку)
	animationStopDelay: 400,    // Задержка конца анимации (ублали мышку)
	zOffset: 55,				// Расстояние, на которое элементы будут отделаться от подложки. Для каждого элемента можно задать индивидуально, напрмиер: <div z='250'></div>
	perspective: 1000,			// То, с какого расстояние пользователь смотрит на элемент. От этого зависит насколько увеличатся элементы. Чем ближе пользователь - тем больше элементы
	zTransition: 400,           // Время, необходимое элементом для приближения
	mode: "default",			// Режимы: default - все элементы отдалены на одинаковое расстояние, 
	//		   topToBottom - расстояние увеличивается сверху вниз с шагом offsetStep, 
	// 		   bootomToTop - расстояние увеличивается снизу вверх с шагом offsetStep
	offsetStep: 35				// Работает только если mode = topToBottom или bootomToTop
};

function Card3d(props) {

	const config = props.config ? { ...defaultConfig, ...props.config } : defaultConfig;

	const Card3dRef = useRef(null);

	const [isHovered, setHovered] = useState(false);

	useState(() => {

	}, [])

	function mouseMoveHandler(e) {
		rotateCard(e.currentTarget, e.clientX, e.clientY)
	}

	function touchMoveHandler(e) {
		rotateCard(e.currentTarget, e.touches[0].clientX, e.touches[0].clientY)
	}

	function mouseEnterHandler(e) {

		animationStart()
	}

	function mouseLeaveHandler(e) {

		animationStop()
	}

	function touchStartHandler(e) {
		animationStart()
	}

	function touchEndHandler(e) {
		animationStop()
	}

	function rotateCard(currentTarget, clientX, clientY) {
		const rect = currentTarget.getBoundingClientRect();
		const [centerX, centerY] = [rect.width / 2, rect.height / 2]
		const [mouseX, mouseY] = [clientX - rect.left, clientY - rect.top]
		const [maxAngleX, maxAngleY] = [config.inverseX ? -config.maxAngleX : config.maxAngleX, config.inverseY ? -config.maxAngleY : config.maxAngleY]
		const [angleX, angleY] = [
			mapRange(centerX - mouseX, -rect.width / 2, rect.width / 2, -maxAngleX, maxAngleX),
			mapRange(centerY - mouseY, -rect.height / 2, rect.height / 2, -maxAngleY, maxAngleY)
		]

		Card3dRef.current.style.transform = `rotateX(${angleY}deg) rotateY(${angleX}deg)`
	}

	function animationStart() {
		setHovered(true);
		Card3dRef.current.style.transition = `transform .${config.animationStartDelay / 100}s`
		setTimeout(() => {
			Card3dRef.current.style.transition = 'transform 0s'
		}, config.animationStartDelay)
	}

	function animationStop() {
		setHovered(false);
		Card3dRef.current.style.transition = `transform .${config.animationStopDelay / 100}s`
		Card3dRef.current.style.transform = `rotateX(0deg) rotateY(0deg)`
	}

	function calcTransfrom(elProps, i, size) {
		let z = 0;
		let propsZ = config.zOffset;
		if (isHovered) {
			if (config.mode === 'default') {
				z = propsZ;
			}
			if (config.mode === 'topToBottom') {
				z = propsZ + i * config.offsetStep
			}
			if (config.mode === 'bottomToTop') {
				z = propsZ + (size - i - 1) * config.offsetStep
			}
			if (elProps.z) {
				z = elProps.z;
			}
		}
		return `translateZ(${z}px)`;
	}

	function childrenElements(children) {
		if (!children) return <></>;
		if (!children.length) children = [children]
		return children.map((element, i, arr) => {
			return (
				<element.type
					{
					...element.props
					}
					key={i}
					style={
						{
							transform: calcTransfrom(element.props, i, arr.length),
							transition: `transform .${config.zTransition / 100}s`,
							transformStyle: 'preserve-3d'
						}
					}
				>
					{
						element.props.children
					}
				</element.type>
			)
		})
	}

	return (
		<div className="card-3d-wrapper"
			onMouseMove={mouseMoveHandler}
			onMouseEnter={mouseEnterHandler}
			onMouseLeave={mouseLeaveHandler}
			onTouchMove={touchMoveHandler}
			onTouchStart={touchStartHandler}
			onTouchEnd={touchEndHandler}
			style={
				{
					perspective: `${config.perspective}px`
				}
			}
		>
			<div
				className={`card-3d-content ${props.className}`}
				style={
					{
						transformStyle: 'preserve-3d'
					}
				}
				ref={Card3dRef}>
				{
					childrenElements(props.children)
				}
			</div>

		</div>
	)
};

function mapRange(value, low1, high1, low2, high2) {
	if (value < low1) value = low1;
	if (value > high1) value = high1;
	return low2 + (high2 - low2) * (value - low1) / (high1 - low1);
}


export default Card3d;
